.hero {
  height: 380px;
  max-width: 320px;

  margin-left: auto;
  margin-right: auto;

  background-position: 0px 0px, -100px 0px;
  background-repeat: no-repeat;
  background-size: auto, cover;

  @include tablet {
    height: 432px;
    max-width: 768px;

    background-position: 0px 0px, 225px 0px;
    background-size: auto, contain;
  }
  @include desktop {
    height: 660px;
    max-width: 1280px;

    background-size: auto, cover;
    background-repeat: no-repeat;
    background-position: 0px -60px, 225px -60px;
  }
}

.hero__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  width: 250px;
  height: 100%;
  padding-top: 40px;

  @include tablet {
    width: 300px;
    padding-top: 0px;
    justify-content: center;
  }
  @include desktop {
    width: 464px;
  }
}

.hero__container {
  padding-left: 20px;
  padding-right: 20px;
  height: 100%;

  @include desktop {
    padding-left: 32px;
    padding-right: 32px;
    width: 1280px;
  }
}

.hero__title {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 38px;

  color: white;

  @include tablet {
    font-size: 44px;
    line-height: 52px;
    // max-width: 250px;
  }
  @include desktop {
    max-height: 500px;
    font-size: 64px;
    line-height: 75px;
    // max-width: 464px;
  }
}

