.hero__text {
  display: block;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;

  margin-top: 8px;
  width: 166px;

  color: white;

  @include tablet {
    font-size: 14px;
    line-height: 16px;

    width: 100%;
  }
  @include desktop {
    // width: 367px;
    font-size: 16px;
    line-height: 19px;

    margin-top: 16px;
  }
}

.hero__movie-descripton {
  display: block;
  width: 100%;
  margin-top: 16px;

  max-height: 80px;

  overflow: hidden;

  @include tablet {
    max-height: 80px;
    max-width: 254px;
  }

  @include desktop {
    max-height: none;
    margin-top: 28px;
    max-width: 457px;
  }
}

.paragraph__hidden {
  display: none;

  @include tablet {
    display: inline;
  }
}

.hero__btn {
  cursor: pointer;
  border-radius: 74px;
  font-family: 'Roboto';

  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #111111;

  min-width: 117px;
  height: 38px;
  margin-top: 16px;
  padding: 12px 24px;

  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);

  transition-property: background, color;
  transition-duration: 300ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  //   transition: color, background, 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @include tablet {
    min-width: 128px;
    height: 40px;
    margin-top: 32px;
    font-size: 14px;
    line-height: 16px;
  }
  @include desktop {
    min-width: 140px;
    height: 48px;
    font-size: 16px;
    line-height: 24px;
  }

  &:hover,
  &:focus {
    border-color: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
    color: #f87719;
    background: white;
  }
}

.hero__vote {
  display: flex;
  align-items: center;
  margin-top: 12px;
  @include desktop {
    margin-top: 16px;
  }
}
